@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 1440px) {
  body {
    position: relative;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.page__body-container {
  background-color: #ffffff;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 1440px) {
  .page__body-container {
    max-width: 1440px;
  }
}

.main-header {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0 auto;
  padding: 0 20px;
  color: #000000;
}
@media (min-width: 768px) {
  .main-header {
    margin: 0 auto;
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    margin: 0 auto;
    width: 1220px;
  }
}

.main-header__logo {
  height: 65px;
  text-align: center;
}
@media (min-width: 768px) {
  .main-header__logo {
    height: 76px;
    padding-top: 24px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    height: 110px;
    padding-top: 53px;
  }
}

.main-header__logo img {
  display: block;
  height: 65px;
  position: relative;
}

.main-header__logo[href]:hover {
  opacity: 0.8;
}
.main-header__logo[href]:hover:active {
  opacity: 0.6;
}

@media (max-width: 767px) {
  .main-nav {
    width: auto;
    min-height: 66px;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__wrapper {
    min-height: 264px;
  }
}

.main-nav--nojs .site-list__item {
  display: block;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__list a {
  color: #000000;
}

@media (min-width: 1440px) {
  .main-nav__list--index a {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
  }
}

.site-list {
