}

.site-list a:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .site-list a:last-child {
    border: none;
  }
}

@media (min-width: 768px) {
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1440px) {
  .site-list__item--active-desktop::after {
    background: #ffffff;
  }
}

@media (min-width: 768px) {
  .site-list__item a[href]:hover {
    opacity: 0.6;
  }
  .site-list__item a[href]:active {
    opacity: 0.3;
  }
}

