.catalog {
  padding: 30px 33px 18px 20px;
  background-color: #f2f2f2;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 77px 319px 57px 53px;
    background-position: 25% 83%;
  }

  @media (min-width: $desktop-width) {
    padding: 77px 51px 55px 52px;
    background-position: 30% 83%;
    margin: 0;
    min-width: 570px;
  }
}

.catalog--loss {
  margin-bottom: 20px;

  &::after {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    top: 21px;
    left: 22px;
    border-radius: 50%;
    background-color: $basic-background;
    z-index: 1;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;

    &::after {
      top: 92px;
      left: 446px;
      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-right: 80px;
    margin-bottom: 0;

    &::after {
      top: 47px;
      left: 52px;
      width: 100px;
      height: 100px;
    }
  }
}

.catalog--weight {
  margin-bottom: 20px;

  &::after {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    top: 21px;
    left: 22px;
    border-radius: 50%;
    background-color: $basic-background;
    z-index: 1;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 45px;

    &::after {
      top: 91px;
      left: 448px;
      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;

    &::after {
      top: 47px;
      left: 52px;
      width: 100px;
      height: 100px;
    }
  }
}

.catalog__icon-loss {
  position: absolute;
  top: 20px;
  left: 30px;
  z-index: 2;

  @media (min-width: $tablet-width) {
    width: 140px;
    height: 194px;
    top: 90px;
    left: auto;
    right: 91px;
  }

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 97px;
    top: 45px;
    left: 70px;
  }
}

.catalog__icon-weight {
  position: absolute;
  top: 40px;
  left: 10px;
  z-index: 2;

  @media (min-width: $tablet-width) {
    width: 268px;
    height: 112px;
    top: 45%;
    left: auto;
    right: 40px;
  }

  @media (min-width: $desktop-width) {
    width: 134px;
    height: 56px;
    top: 80px;
    left: 30px;
  }
}

.catalog__title {
  @include catalog-title;
  margin: 0;
  margin-left: 71px;

  @media (min-width: $tablet-width) {
    @include heading;
    line-height: 36px;
    margin: 0;
    margin-bottom: 24px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    text-align: center;
    margin-bottom: 73px;
  }
}

.catalog__intro {
  font-family: "Arial", sans-serif;
  margin-bottom: 38px;
  position: relative;

  @media (max-width: $mobile-width) {
    &::after {
      content: "";
      position: absolute;
      width: 240px;
      height: 1px;
      bottom: -23px;
      left: 0;
      background-color: #d9d9d9;
    }
  }

  @media (min-width: $tablet-width) {
    @include cover-text;
    line-height: 24px;
    color: #000000;
    margin-bottom: 52px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 27px;
  }
}

.catalog__service {
  @include text;
  display: block;
  text-decoration: none;

  &:active {
    opacity: 0.3;
  }
}

.catalog__service-arrow {
  margin-left: 17px;
  transform: scaleX(1);
  transform-origin: center left;
  transition: transform 0.5s;
}

.catalog__service:hover .catalog__service-arrow {
  transform: scaleX(1.4);
}

.catalog__service:active .catalog__service-arrow {
  transform: scaleX(1.4);
  opacity: 0.3;
}
